import React, { useEffect, useRef, useState } from 'react';
import { useParams, Navigate, useNavigate } from 'react-router-dom';
import { useCaseStore, useAuthStore } from '@/stores';
import { LoadingSpinner, Button } from '@/components/common';
import { CASE_STATUS } from '@/constants';
import { MapComponent, VitalSignsComponent, ChatComponent, TreatmentComponent, LocationTracker, BuildingInfoComponent } from '@/components/features';
import { RouteOptimizationComponent } from '@/components/features/RouteOptimization';

export const CaseDetailPage: React.FC = () => {
  const { caseId } = useParams<{ caseId: string }>();
  const navigate = useNavigate();
  const { currentCase, loading, error, loadCurrentCase, startObservingCase, stopObservingCase, updateStatus } = useCaseStore();
  const { userInfo } = useAuthStore();
  const isMountedRef = useRef(true);
  const [activeTab, setActiveTab] = useState<'vitals' | 'treatments'>('vitals');
  const [currentLocation, setCurrentLocation] = useState<{ latitude: number; longitude: number } | null>(null);
  const [selectedRoute, setSelectedRoute] = useState<any>(null);
  const [selectedHospital, setSelectedHospital] = useState<any>(null);
  
  // 
  const handleStatusUpdate = async (newStatus: string) => {
    if (!caseId) return;
    
    try {
      await updateStatus(caseId, newStatus as any);
    } catch (error) {
      console.error('Status update failed:', error);
      alert('');
    }
  };
  
  // 
  const getNextStatus = (currentStatus: string) => {
    switch (currentStatus) {
      case CASE_STATUS.DISPATCHED:
        return { status: CASE_STATUS.ON_SCENE, label: '' };
      case CASE_STATUS.ON_SCENE:
        return { status: CASE_STATUS.TRANSPORTING, label: '' };
      case CASE_STATUS.TRANSPORTING:
        return { status: CASE_STATUS.COMPLETED, label: '' };
      default:
        return null;
    }
  };
  
  // 
  const handleLocationUpdate = (location: { latitude: number; longitude: number }) => {
    setCurrentLocation(location);
  };
  
  useEffect(() => {
    isMountedRef.current = true;
    
    if (!caseId) return;
    
    const loadData = async () => {
      try {
        if (isMountedRef.current) {
          await loadCurrentCase(caseId);
          if (isMountedRef.current) {
            startObservingCase(caseId);
          }
        }
      } catch (error) {
        console.error('Failed to load case:', error);
      }
    };
    
    loadData();
    
    return () => {
      isMountedRef.current = false;
      try {
        stopObservingCase();
      } catch (error) {
        console.error('Cleanup error:', error);
      }
    };
  }, [caseId]);
  
  // 
  useEffect(() => {
    return () => {
      isMountedRef.current = false;
    };
  }, []);
  
  if (!caseId) {
    return <Navigate to="/cases" replace />;
  }
  
  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <LoadingSpinner size="lg" />
      </div>
    );
  }
  
  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <h1 className="text-2xl font-bold text-gray-900 mb-4"></h1>
          <p className="text-gray-600 mb-6">{error}</p>
          <button 
            onClick={() => {
              isMountedRef.current = false;
              try {
                stopObservingCase();
              } catch (error) {
                console.error('Stop observing error:', error);
              }
              
              // 
              setTimeout(() => {
                try {
                  navigate('/cases');
                } catch (error) {
                  console.error('Navigation error:', error);
                  window.location.href = '/cases';
                }
              }, 100);
            }}
            className="bg-medical-primary text-white px-4 py-2 rounded-lg hover:bg-blue-700"
          >
            
          </button>
        </div>
      </div>
    );
  }
  
  if (!currentCase) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <h1 className="text-2xl font-bold text-gray-900 mb-4"></h1>
          <p className="text-gray-600 mb-6"></p>
          <button 
            onClick={() => {
              isMountedRef.current = false;
              try {
                stopObservingCase();
              } catch (error) {
                console.error('Stop observing error:', error);
              }
              
              // 
              setTimeout(() => {
                try {
                  navigate('/cases');
                } catch (error) {
                  console.error('Navigation error:', error);
                  window.location.href = '/cases';
                }
              }, 100);
            }}
            className="bg-medical-primary text-white px-4 py-2 rounded-lg hover:bg-blue-700"
          >
            
          </button>
        </div>
      </div>
    );
  }
  
  return (
    <div className="min-h-screen bg-gray-50">
      {/*  */}
      <header className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center space-x-4">
              <button
                onClick={() => {
                  isMountedRef.current = false;
                  try {
                    stopObservingCase();
                  } catch (error) {
                    console.error('Stop observing error:', error);
                  }
                  
                  // 
                  setTimeout(() => {
                    try {
                      navigate('/cases');
                    } catch (error) {
                      console.error('Navigation error:', error);
                      window.location.href = '/cases';
                    }
                  }, 100);
                }}
                className="p-2 text-gray-400 hover:text-gray-600 transition-colors"
              >
                <svg className="h-5 w-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 19l-7-7 7-7" />
                </svg>
              </button>
              
              <div>
                <h1 className="text-xl font-semibold text-gray-900">{currentCase.caseName}</h1>
                <p className="text-sm text-gray-500">
                  : {currentCase.status} | : {currentCase.teamId}
                </p>
              </div>
            </div>
            
            <div className="flex items-center space-x-4">
              {(() => {
                const nextStatus = getNextStatus(currentCase.status);
                return nextStatus ? (
                  <Button
                    size="sm"
                    onClick={() => handleStatusUpdate(nextStatus.status)}
                  >
                    {nextStatus.label}
                  </Button>
                ) : null;
              })()}
              
              <Button
                size="sm"
                variant="outline"
                onClick={() => navigate(`/cases/${caseId}/registry`)}
              >
                
              </Button>
              
              <div className="text-sm text-gray-700">
                <span className="font-medium">{userInfo?.name}</span>
              </div>
            </div>
          </div>
        </div>
      </header>

      {/*  */}
      <main className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">
        <div className="px-4 py-6 sm:px-0">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/*  */}
            <div className="bg-white rounded-lg shadow p-6">
              <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
              <div className="h-64">
                <MapComponent
                  sceneLocation={currentCase.sceneLocation}
                  hospitalLocation={currentCase.hospitalLocation}
                  currentLocation={currentLocation || undefined}
                  selectedRoute={selectedRoute}
                  selectedHospital={selectedHospital}
                  showFullscreenButton={true}
                />
              </div>
              <div className="mt-3 text-sm text-gray-600">
                <div className="flex items-center justify-between">
                  <div className="flex items-center space-x-4">
                    <div className="flex items-center">
                      <div className="w-3 h-3 bg-red-600 rounded-full mr-1"></div>
                      <span></span>
                    </div>
                    <div className="flex items-center">
                      <div className="w-3 h-3 bg-blue-600 rounded-full mr-1"></div>
                      <span></span>
                    </div>
                    {currentLocation && (
                      <div className="flex items-center">
                        <div className="w-3 h-3 bg-green-600 rounded-full mr-1"></div>
                        <span></span>
                      </div>
                    )}
                    {selectedRoute && (
                      <div className="flex items-center">
                        <div className="w-4 h-1 bg-blue-500 rounded mr-1"></div>
                        <span></span>
                      </div>
                    )}
                  </div>
                  <div className="text-xs text-gray-500">
                     
                  </div>
                </div>
              </div>
              
              {/*  */}
              <div className="mt-4">
                <LocationTracker onLocationUpdate={handleLocationUpdate} />
              </div>
              
              {/*  */}
              {currentCase.patientInfo.buildingInfo && (
                <div className="mt-4 bg-blue-50 rounded-lg p-4 border border-blue-200">
                  <h4 className="text-sm font-medium text-blue-900 mb-2"> </h4>
                  <div className="text-sm text-blue-800">
                    <div className="mb-1">
                      <span className="font-medium">:</span> {currentCase.patientInfo.buildingInfo.buildingName}
                    </div>
                    <div className="mb-1">
                      <span className="font-medium">:</span> {currentCase.patientInfo.buildingInfo.floor}
                    </div>
                    {currentCase.patientInfo.buildingInfo.description && (
                      <div className="mb-1">
                        <span className="font-medium">:</span> {currentCase.patientInfo.buildingInfo.description}
                      </div>
                    )}
                    {currentCase.patientInfo.buildingInfo.accessNotes && (
                      <div className="mt-2 p-2 bg-blue-100 rounded">
                        <span className="font-medium">:</span><br />
                        {currentCase.patientInfo.buildingInfo.accessNotes}
                      </div>
                    )}
                  </div>
                </div>
              )}
              
              {/*  */}
              <div className="mt-4">
                <BuildingInfoComponent location={currentCase.sceneLocation} />
              </div>
              
              {/*  */}
              <div className="mt-6 border-t pt-4">
                <h4 className="text-md font-medium text-gray-900 mb-4"> </h4>
                <RouteOptimizationComponent
                  currentLocation={currentLocation || undefined}
                  sceneLocation={{
                    latitude: currentCase.sceneLocation.latitude,
                    longitude: currentCase.sceneLocation.longitude
                  }}
                  onRouteSelected={(route) => {
                    console.log('Route selected:', route);
                    console.log('Route coordinates:', route?.coordinates);
                    console.log('Route coordinates length:', route?.coordinates?.length);
                    setSelectedRoute(route);
                  }}
                  onNavigationStart={(route) => {
                    console.log('Navigation started:', route);
                    alert('');
                  }}
                  onHospitalSelected={(hospital) => {
                    console.log('Hospital selected:', hospital);
                    setSelectedHospital(hospital);
                  }}
                />
              </div>
            </div>
            
            {/*  */}
            <div className="bg-white rounded-lg shadow p-6">
              <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
              
              {currentCase.patientInfo.name && (
                <div className="mb-3">
                  <span className="text-sm font-medium text-gray-500">: </span>
                  <span className="text-sm text-gray-900">{currentCase.patientInfo.name}</span>
                </div>
              )}
              
              {currentCase.patientInfo.age && (
                <div className="mb-3">
                  <span className="text-sm font-medium text-gray-500">: </span>
                  <span className="text-sm text-gray-900">{currentCase.patientInfo.age}</span>
                </div>
              )}
              
              {currentCase.patientInfo.gender && (
                <div className="mb-3">
                  <span className="text-sm font-medium text-gray-500">: </span>
                  <span className="text-sm text-gray-900">
                    {currentCase.patientInfo.gender === 'male' ? '' : 
                     currentCase.patientInfo.gender === 'female' ? '' : ''}
                  </span>
                </div>
              )}
              
              {/*  */}
              <div className="mt-6 border-b border-gray-200">
                <nav className="-mb-px flex space-x-8">
                  <button
                    onClick={() => setActiveTab('vitals')}
                    className={`py-2 px-1 border-b-2 font-medium text-sm ${
                      activeTab === 'vitals'
                        ? 'border-medical-primary text-medical-primary'
                        : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                    }`}
                  >
                    
                  </button>
                  <button
                    onClick={() => setActiveTab('treatments')}
                    className={`py-2 px-1 border-b-2 font-medium text-sm ${
                      activeTab === 'treatments'
                        ? 'border-medical-primary text-medical-primary'
                        : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                    }`}
                  >
                    
                  </button>
                </nav>
              </div>

              {/*  */}
              <div className="mt-4">
                {activeTab === 'vitals' ? (
                  <VitalSignsComponent caseId={caseId!} />
                ) : (
                  <TreatmentComponent caseId={caseId!} />
                )}
              </div>
            </div>
          </div>
          
          {/*  */}
          <div className="mt-6 bg-white rounded-lg shadow p-6">
            <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
            <div className="h-64">
              <ChatComponent caseId={caseId!} />
            </div>
          </div>
        </div>
      </main>
    </div>
  );
};